import './App.css';
import { useCallback, useState } from 'react';
import { Box, Button } from '@mui/material';
import Fact from './Fact';

function App() {
  const total = 20;
  const [res, setRes] = useState(0);
  const updateResult = useCallback((update) => {
    setRes(res + update);
  }, [res]);

  return (
    <div className="App">
      <Box sx={{ display: 'grid', gridTemplateColumns: 'repeat(2, 1fr)' }}>
        {[...Array(total).keys()].map(_i => <Fact updateResult={updateResult} />)}
      </Box>
      <Box sx={{ fontSize: '2rem', m: 1 }}>{res} / {total}</Box>
      <Button variant="contained" onClick={() => window.location.reload()}>Try More</Button>
    </div>
  );
}

export default App;
