import { useCallback, useMemo, useState } from 'react';
import * as React from "react";
import CheckIcon from '@mui/icons-material/Check';
import ClearIcon from '@mui/icons-material/Clear';
import { Box, Select, MenuItem } from '@mui/material';

function Fact({ updateResult }) {
  const [a, b, sign] = useMemo(() => {
    const sign = Math.random() > 0.5;
    let a, b;
    if (sign) {
      a = (Math.ceil(Math.random() * 10));
      b = (Math.ceil(Math.random() * 20));
    } else {
      a = Math.ceil(Math.random() * 10);
      b = Math.ceil(Math.random() * 10) + a;
    }
    return [a, b, sign];
  }, []);

  const [isCorrect, setIsCorrect] = useState(null);
  const [res, setRes] = useState(null);

  const checkResult = useCallback((event) => {
    const res = event.target.value;
    const c = (res === (sign ? a + b : b - a)) ? true : false;
    setRes(res);
    if (c !== isCorrect && isCorrect !== null) {
      if (c) {
        updateResult(1);
      } else {
        updateResult(-1);
      }
    }
    setIsCorrect(c);
  }, [sign, a, b, isCorrect, updateResult]);

  return (
    <Box elevation={3} sx={{ m: 1, p: 1, fontSize: '2rem' }}>
      {b} {sign ? "+" : "-"} {a} =
      <Select
        value={res}
        label="Answer"
        onChange={checkResult}
        sx={{ m: 1 }}
      >
        {[...Array(30).keys()].map(i =>
          <MenuItem value={i} sx={{ fontSize: '2rem' }} > {i}</MenuItem>)}
      </Select>
      {isCorrect === null ? null : isCorrect === true ? <CheckIcon /> : <ClearIcon />}
    </Box >
  );
}

export default React.memo(Fact);
